.single-question.first-question {
  & .question_inputs-wrapper {
    padding: 0 88px;
  }

  & .question_pagination {
    margin: 0 63px;
  }

  & .question_next {
    width: 100%;
  }

  & .question_attention {
    padding: 0 63px;
  }
}

.question_inputs-wrapper {
  padding: 0 73px;
  display: flex;
  flex-direction: column;
}

.question {
  background: white;
  box-shadow: 0px 8px 40px -10px rgba(0, 0, 0, 0.18);
  border-radius: 10px;
  max-width: 530px;
  box-sizing: border-box;
  padding: 50px 32px;
}

.question_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 110%;
  text-align: center;
  color: #0F0E0E;
  margin-bottom: 30px;

  & span {
    color: #E21313;
  }
}

.question_steps {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 40px;

  & span {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 120%;
    color:#15AF40;
    display: flex;
    flex-direction: row;
    align-items: center;

    &.active {
      color: #15AF40;

      &::after {
        background-image: url("data:image/svg+xml,%0A%3Csvg width='11' height='18' viewBox='0 0 11 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00051 9L0 1.99969L1.99974 0L11 9L1.99974 18L0 16.0003L7.00051 9Z' fill='%2315AF40'/%3E%3C/svg%3E%0A");
      }
    }

    &.active ~ span {
      color:  #A7A8B0;

      &::after {
        background-image: url("data:image/svg+xml,%0A%3Csvg width='11' height='18' viewBox='0 0 11 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00051 9L0 1.99969L1.99974 0L11 9L1.99974 18L0 16.0003L7.00051 9Z' fill='%23A7A8B0'/%3E%3C/svg%3E%0A");
      }
    }

    &::before {
      content: "Шаг";
      margin-right: 3px;
    }

    &::after {
      content: "";
      display: inline-block;
      width: 11px;
      height: 18px;
      background-image: url("data:image/svg+xml,%0A%3Csvg width='11' height='18' viewBox='0 0 11 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00051 9L0 1.99969L1.99974 0L11 9L1.99974 18L0 16.0003L7.00051 9Z' fill='%2315AF40'/%3E%3C/svg%3E%0A");
      margin-left: 11px;
      margin-right: 19px;
    }
  }
}

.question_steps span:last-child::after {
  margin-right: 0;
}

.question_pagination {
  padding-bottom: 15px;
  border-bottom: 1px solid #C6C6C6;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px 73px 0 73px;
}

.question_next {
  background: #2196F3;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 140%;
  color: #FFFFFF;
  border: none;
  padding: 6px 15px;
  height: 60px;
  width: 180px;

  &:hover {
    cursor: pointer;
  }

  &:disabled {
    background: #E3E3E3;

    &:hover {
      cursor: auto;
    }
  }
}

.question_prev {
  background: #92AC9E;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 140%;
  color: #FFFFFF;
  border: none;
  padding: 0;
  height: 60px;
  width: 120px;

  &:hover {
    cursor: pointer;
  }
}

.question_attention {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  text-align: center;
  color: #000000;
  margin-top: 30px;
  padding: 0 73px;
}

.question_input-title {
  padding-left: 12px;
  margin-bottom: 10px;
}

.question_text-input {
  width: 100%;
  padding: 14px 12px;
  box-sizing: border-box;
  border: 1px solid #C6C6C6;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 100%;
  color: black;
  text-align: left;
  z-index: 1;
  position: relative;
  background: transparent;
  margin-bottom: 20px;

  &::placeholder {
    color: #B4B4B4;
  }

  &:hover,
  &:focus {
    border-color: #999999;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat;
  }
}

.question_protection {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  color: #BD4D4E;
  text-align: center;
  margin-bottom: 14px;

  & img {
    margin-right: 12px;
  }
}

#accepting {
  display: none;
}

#accepting:checked ~ .question_accepting::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5L6.33333 9L13 1' stroke='%232B4FA9'/%3E%3C/svg%3E%0A") center no-repeat;
}

.question_accepting {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120%;
  text-align: center;
  color: #2B4FA9;
  margin: 0 73px;
  margin-bottom: 10px;
  position: relative;
  display: inline-block;
  padding-left: 30px;

  &:hover {
    cursor: pointer;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid #2B4FA9;
    background: none;
    position: absolute;
    top: calc(50% - 10px);
    left: 0;
  }
}

.question_submit {
  background: #2196F3;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 140%;
  color: #FFFFFF;
  border: none;
  padding: 6px 15px;
  height: 60px;
  width: 100%;

  &:hover {
    cursor: pointer;
  }

  &:disabled {
    opacity: 0.5;

    &:hover {
      cursor: auto;
    }
  }
}

@media(max-width: 1043px) {
  .single-question.first-question {
    & .question_pagination {
      margin-top: 7px;
    }

    & .question_inputs-wrapper {
      padding: 0 45px;
    }

    & .question_pagination {
      margin: 0 15px;
    }

    & .question_attention {
      padding: 0 15px;
    }

    & .question_next {
      flex-basis: auto;
    }
  }
}

@media(max-width: 1043px) {
  .question {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 100%;
    box-sizing: border-box;
    box-shadow: none;
    max-width: none;
  }

  .question_title {
    order: 2;
    font-size: 24px;
    line-height: 110%;
  }

  .question_steps {
    order: 1;
    background: #F5F5F5;
    padding: 14px 0;
    margin-bottom: 30px;
  }

  .question_inputs-wrapper {
    order: 3;
    padding: 0 15px;
  }

  .question_pagination {
    order: 4;
    margin: 0 15px;
    padding-bottom: 10px;
  }

  .question_prev,
  .question_next {
    width: auto;
    flex-basis: 136px;
    box-sizing: border-box;
    padding-left: 0;
    padding-right: 0;
    font-size: 17px;
    line-height: 140%;
  }

  .question_attention {
    order: 5;
    padding: 0 15px;
  }

  .question_protection {
    order: 6;
  }

  .question_accepting {
    order: 7;
  }

  .single-question.result {
    & .question_title {
      font-size: 21px;
      padding-right: 35px;
      padding-left: 35px;
    }

    & .question_protection {
      order: 4;
      font-size: 16px;
      line-height: 120%;
    }

    & .question_pagination {
      order: 6;
      padding-bottom: 15px;
    }

    & .question_accepting {
      order: 5;
      margin: 0 42px 24px;
    }

    & .question_attention {
      order: 7;
      font-size: 16px;
    }
  }
}


