.custom-select {
  position: relative;
  margin-bottom: 20px;
}

.custom-select_selected-option-wrapper {
  position: relative;

  & svg {
    position: absolute;
    right: 15px;
    top: 13px;
    transition: transform 0.3s ease-in-out;
  }
}

.custom-select.open .custom-select_selected-option-wrapper svg {
  transform: rotate(-180deg);
}

.custom-select_selected-option {
  width: 100%;
  padding: 14px 12px;
  box-sizing: border-box;
  border: 1px solid #C6C6C6;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 100%;
  color: black;
  text-align: left;
  z-index: 1;
  position: relative;
  background: transparent;

  &::placeholder {
    color: #B4B4B4;
  }

  &:hover {
    cursor: pointer;
    border-color: #999999;
  }

  &:hover,
  &:focus,
  &:active {
    outline: 0;
    outline-offset: 0;
  }
}

.custom-select_options {
  padding-left: 0;
  list-style: none;
  padding: 14px 12px;
  margin: 0;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  background: white;
  height: 0;
  transition: height 1s ease-out;
  transform: scaleY(0);
}

.custom-select.open .custom-select_options {
  transform: scaleY(1);
  transform-origin: top;
  transition: transform 0.4s ease;
  height: auto;
}

.custom-select_option {
  text-align: left;
  margin-top: 0;
  margin-bottom: 10px;
  color: #B4B4B4;

  &:hover {
    color: black;
    cursor: pointer;
  }
}
