
body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
}

.background {
  width: 1280px;
  height: 1280px;
  background: #E21313;
  border-radius: 50%;
  position: absolute;
  top: -612px;
  left: calc(50% - 1280px / 2);
  z-index: -1;
}

.center {
  max-width: 1043px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 1043px) {
  .center {
    max-width: none;
    margin: 0;
  }
}

*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0;
}

p {
  margin: 0;
}

footer {
  text-align: center;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 160%;
  color: #828282;
  margin-top: auto;
  padding-top: 50px;
}

@media(max-width: 1043px) {
  footer {
    padding-top: 80px;
  }
}

.loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,.3);
  z-index: -99;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  &::before {
    content: url(../img/loader.svg);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &.active {
    z-index: 99;
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}

.disable_page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: white;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 110%;
  color: #0F0E0E;

  & span {
    display: block;
    font-weight: normal;
  }

  &.active {
    display: flex;
  }
}
