.single-question {
  display: none;
  flex-direction: column;

  &.active {
    display: flex;
  }

  &.first-question .single-question_body {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  &.first-question .single-question_logo {
    margin-top: 20px;
  }

  &.first-question .single-question_question {
    margin-left: 40px;
    margin-right: 40px;
  }
}

.error-display {
  display: none;

  &.active {
    display: block;
  }
}

.single-question_header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 50px 60px;
}

.single-question_item {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 15px;
  margin-left: 15px;
}

.single-question_img {
  margin-right: 15px;
}

.single-question_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: #FFFFFF;
  margin: 0;

  & span {
    display: block;
    font-weight: 700;
  }
}

.single-question_logo {
  margin-top: 30px;
  margin-bottom: 30px;
}

.single-question_body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.single-question_terms {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.single-question_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120%;
  color: #FFFFFF;
  margin-bottom: 20px;

  & span {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 27px;
    line-height: 120%;
    color: #FFFFFF;
    display: block;
    margin-top: 6px;
  }
}

@media(max-width: 1043px) {
  .background {
    display: none;
  }

  .single-question.first-question {
    & .single-question_question {
      margin: 0;
      max-width: none;
    }

    & .single-question_body {
      order: 1;
      display: flex;
      flex-direction: column;
    }

    & .single-question_logo {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  .single-question_logo {
    margin-top: 0;
    padding-bottom: 10px;
    padding-top: 10px;
    background: #E21313;
    width: 100%;
    margin-bottom: 0;
    padding-right: 15px;
    padding-left: 15px;
    box-sizing: border-box;

    & img {
      width: 73px;
      height: auto;
    }
  }

  .single-question_description {
    display: none;
  }

  .single-question_header {
    order: 2;
    padding: 0 15px 15px 10px;
    flex-direction: column;
    align-items: center;
  }

  .single-question_item {
    justify-content: flex-start;
    flex-shrink: 0;
    margin-bottom: 12px;
    min-width: 190px;
    margin-left: 0;
    margin-right: 0;
  }

  .single-question_img {
    fill: #E21313;
    margin-right: 10px;
  }

  .single-question_title {
    color: #0F0E0E;
    font-size: 14px;
    line-height: 120%;

    & span {
      font-size: 14px;
      line-height: 120%;
    }
  }

  .error-display {
    & .question_title {
      padding: 0 15px;
    }
  }
}
