.input-with-steps {
  display: flex;
  flex-direction: column;

  & > div {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 30px;
  }
}

.input-with-steps_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 110%;
  text-align: center;
  color: #727272;
  margin-bottom: 12px;
}

.input-with-steps_button-step {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 1px solid #E21313;
  flex-shrink: 0;
  flex-grow: 0;

  &:hover {
    cursor: pointer;
  }

  &--decrease {
    position: relative;

    &::before {
      content: "";
      display: block;
      width: 8px;
      height: 1px;
      background: #E21313;
      position: absolute;
      top: calc(50% - 0.5px);
      right: calc(50% - 4px);
    }
  }

  &--increase {
    position: relative;

    &::before,
    &::after {
      content: "";
      display: block;
      width: 8px;
      height: 1px;
      background: #E21313;
      position: absolute;
      top: calc(50% - 0.5px);
      right: calc(50% - 4px);
    }

    &::after {
      transform: rotate(90deg);
    }
  }
}

.input-with-steps_input {
  width: 100%;
  background: transparent;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 110%;
  color: #E21313;
  margin: 0 20px;
  text-align: center;

  &::placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 110%;
    color: #A7A8B0;
  }
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

@media(max-width: 1043px) {
  .input-with-steps_description {
    margin-bottom: 7px;
  }

  .input-with-steps > div {
    margin-bottom: 23px;
  }

  .question_input-title {
    margin-bottom: 10px;
  }
}
